<template>
  <div class="news-template g-in">
    <div class="news-list-top clearfix">
      <!-- 搜索 -->
      <div class="fr">
        <el-input placeholder="搜索新闻动态..." v-model.trim="searchInfo" class="input-with-select" clearable>
          <el-button slot="append" class="search-btn" icon="el-icon-search" @click="searchData"></el-button>
        </el-input>
      </div>
    </div>
   
    <div class="news-list-content">
      <ul class="list">
        <p class="noData" v-if="noDataText">抱歉，未找到您所需要的内容</p>
        <li class="clearfix" v-for="item in newsList" :key="item.id">
          <p class="content fl" @click="goDetail(item)">{{ item.title }}</p>
          <p class="time fr">{{ item.publishTime&&item.publishTime.split(' ')[0] }}</p>
        </li>
      </ul>
      <el-pagination background layout="prev, pager, next" :total="total" :current-page="pageInfo.pageNumber" @current-change="handleCurrentChange"></el-pagination>
    </div>

  </div>
</template>

<script>
import Cookies from 'js-cookie'
export default {
  data() {
    return {
      searchInfo: '',
      pageInfo: {
        pageNumber: 1,
        pageSize: 10
      },
      newsList: [],
      total: 0,
      noDataText: false
    }
  },
  created() {
    this.getNewsInfo()
  },
  methods: {
    // 请求新闻数据
    async getNewsInfo() {
      const res = await this.$ajaxPost(this.projectName + '/news/findAll.do')
      this.codeId = res[0].classesId
      this.ajaxData()
    },
    // 发送数据请求
    async ajaxData() {
      const res = await this.$ajaxPost(this.projectName + '/news/finds.do', {
        classesId: this.codeId,
        ...this.pageInfo
      })
      // console.log(res)
      this.noDataText = res.total === 0
      this.newsList = res.rows
      this.total = res.total
    },
    // 分页
    handleCurrentChange(val) {
      this.pageInfo.pageNumber = val
      this.ajaxData()
    },
    // 搜索
    async searchData() {
      this.pageInfo.pageNumber = 1
      if(!this.searchInfo) {
        this.$message.warning('请输入搜索内容...')
        return
      }
      const res = await this.$ajaxPost(this.projectName + '/news/finds.do', {
        classesId: this.codeId,
        ...this.pageInfo,
        title: this.searchInfo
      })
      // console.log(res)
      this.noDataText = res.total === 0
      this.newsList = res.rows
      this.total = res.total
    },
    // 进入详情
    goDetail(item) {
      // console.log(item)
      this.$router.push({
        name: 'newsdetail',
        params: {
          id: item.id
        }
      })
    }
  }
}
</script>

<style lang="scss">
.news-template{
  min-height: 600px;
  margin: 40px auto !important;
  .news-list-top{
    margin: 0 0 20px;
    .el-tabs{
      margin-left: 5px;
      .el-tabs__nav{
        .el-tabs__item{
          font-size: 16px;
          color: #797979;
        }
        .el-tabs__item.is-active{
          color: #00aec4 !important;
        }
      }
    }
    .el-input{
      .el-input-group__append{
        background-color: #f5f7fa;
        color: #909399;
        border: 1px solid #dcdfe6;
        border-left: none;
      }
    }
  }
  .news-list-content{
    background-color: #fff;
    border:1px solid #E4E4E4;
    box-sizing: border-box;
    padding: 10px;
    .el-breadcrumb{
      font-size: 12px;
      margin-bottom: 10px;
    }
    .list{
      box-sizing: border-box;
      padding: 0 30px;
      .noData{
        text-align: center;
        color: #ccc;
      }
      li{
        height: 56px;
        border-bottom: 1px solid #E8E8E8;
        cursor: pointer;
        &:last-child{
          border-bottom: 0;
        }
        &:hover{
          background-color: #FBFBFB;
          border-radius: 4px;
        }
        &:hover p.content, &:focus p.content{
          color: #00aec4;
        }
        &:hover .content::before, &:focus .content::before{
          background-color: #00aec4 !important;
        }
        .classes-tip{
          height: 100%;
          line-height: 56px;
          margin: 0;
          font-size: 12px;
          color: #fff;
          span{
            background-color: #00aec4;
            padding: 2px 4px;
            border-radius: 2px;
          }
        }
        .content{
          width: 80%;
          height: 100%;
          margin: 0;
          line-height: 56px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          color: #4A4A4A;
          font-size: 15px;
          box-sizing: border-box;
          padding-left: 20px;
          position: relative;
          &:before{
            content: '';
            display: block;
            width: 6px;
            height: 6px;
            background-color: #D8D8D8;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate(0, -50%);
          }
        }
        .all-content{
          width: 80%;
          height: 100%;
          margin: 0;
          line-height: 56px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          color: #4A4A4A;
          font-size: 15px;
          box-sizing: border-box;
          padding-left: 20px;
        }
        .time{
          font-size: 12px;
          color: #797979;
          height: 100%;
          margin: 0;
          line-height: 56px;
        }
      }
    }
    .el-pagination{
      box-sizing: border-box;
      padding: 10px 20px;
      text-align: right;
    }
  }
}
</style>



