<template>
  <div class="news-details g-in">
    <!-- 面包屑 -->
    <div class="detail-bread">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item v-for="(item,index) in breadList" :key="index" :to="item.name?{name: item.name}:''">{{ item.title }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="news-detail-contents">
      <div class="detail-top">
        <h1>{{ newsDetail.title }}</h1>
        <p>发布时间：<span>{{newsDetail.publishTime&&newsDetail.publishTime.split(' ')[0]}}</span>&emsp;&emsp;&emsp;&emsp;来源：<span>{{ newsDetail.source }}</span></p>
      </div>
      <div class="detail-content" v-html="newsDetail.content"></div>
    </div>
    <BackTop></BackTop>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
export default {
  data() {
    return {
      breadList: this.$route.meta.bc,
      newsId: this.$route.params.id,
      newsDetail: {}
    }
  },
  created() {
    this.getRouterInfo()
  },
  methods: {
    getRouterInfo() {
      this.ajaxData()
    },
    // 请求新闻 数据
    async ajaxData() {
      const res = await this.$ajaxPost(this.projectName + '/news/find.do', {
        id: this.newsId
      })
      // console.log(res)
      this.newsDetail = res
    }
  }
}
</script>

<style lang="scss">
.news-details{
  min-height: 600px;
  .detail-bread{
    background-color: #FFF;
    box-sizing: border-box;
    padding: 0 20px;
    height: 48px;
    margin: 50px 0 20px;
    .el-breadcrumb{
      line-height: 48px;
    }
  }
  .news-detail-contents{
    background-color: #FFF;
    box-sizing: border-box;
    padding: 35px 20px;
    margin-bottom: 80px;
    .detail-top{
      width: 100%;
      border-bottom: 1px solid #E8E8E8;
      h1{
        font-weight: 400;
        font-size: 24px;
        text-align: center;
        width: 80%;
        margin: 0 auto;
        line-height: 1.5;
      }
      p{
        margin: 30px 0;
        text-align: center;
      }
    }
    .detail-content{
      box-sizing: border-box;
      padding: 24px;
      line-height: 1.5;
    }
  }
}
</style>



